<template>
    <div id="weiboData" :style="{width: yellowWidth+'px',height: yellowHeight+'px'}">
        <div id="dataName" :style="{lineHeight: nameLineHeight+'px', fontSize: spFont+'px'}">
            <span id="dataNameSp1" style="font-weight: bold;">{{title}}</span>
            <span id="dataNameSp2" v-if="!weiboDatas.haveBFH" v-show="nameShow">（条）</span>
        </div>
        <div id="dataNum" :style="{color: color, fontSize: numFont+'px'}">
            <span>{{showNum}}</span>
            <span v-if="weiboDatas.haveBFH"></span>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'WeiboData',
        props: ['weiboDatas'], //使用haveBFH确定百分号和条字
        data(){
            return {
                title: this.weiboDatas.title,  //标题
                color: this.weiboDatas.color,  //颜色
                showNum: 0,  //展示的数值

                //样式数值动态绑定
                yellowWidth: 250,  //数据框宽
                yellowHeight: 206,  //数据框高
                nameLineHeight: 40,  //数据标题行高
                nameShow: true,
                spFont: 16,  //标题前半部分字号
                numFont: 40,  //数字字号
            }
        },
        mounted(){
            //最开始调用一遍增长动画
            this.NumAutoPlus(100)
        },
        methods:{
            //展示数值增长动画
            NumAutoPlus(timeUse){
                let initial = this.showNum
                let endNum = this.num
                let step = (endNum - initial) / timeUse
                let count = initial

                let timer = setInterval(()=>{
                    count = count + step
                    if(count >= endNum) { 
                        clearInterval(timer)
                        count = endNum
                    }
                    this.showNum = Math.floor(count)
                },30)
            },
            //样式调整函数
            resize(w, h){ 
                let wDataName = document.getElementById('dataName')

                //为了显示效果适当隐藏部分字
                if(w<580)
                {
                    this.nameShow = false
                }
                else
                {
                    this.nameShow = true
                }

                if(w<500)
                {
                    this.yellowWidth = w*0.85*0.2
                    this.yellowHeight = h*0.55
                    this.nameLineHeight = parseFloat(getComputedStyle(wDataName).height)
                    this.spFont = 12
                    this.numFont = 12
                }
                else if(w>=500 && w<800)
                {
                    this.yellowWidth = w*0.85*0.2
                    this.yellowHeight = h*0.55
                    this.nameLineHeight = parseFloat(getComputedStyle(wDataName).height)
                    this.spFont = 14
                    this.numFont = 18
                }
                else if(w>=800 && w<1300)
                {
                    this.yellowWidth = w*0.85*0.2
                    this.yellowHeight = h*0.55
                    this.nameLineHeight = parseFloat(getComputedStyle(wDataName).height)
                    this.spFont = 16
                    this.numFont = 30
                }
                else if(w>=1300)
                {
                    this.yellowWidth = 250
                    if(h<370)
                    {
                        this.yellowHeight = 206
                    }
                    else
                    {
                        this.yellowHeight = h*0.55
                    }
                    this.nameLineHeight = parseFloat(getComputedStyle(wDataName).height)
                    this.spFont = 16
                    this.numFont = 40
                } 
            }
        },
        watch:{
            //监视原始数值，改变时使用增长动画
            num(){
                this.NumAutoPlus(100)
            }
        },
        computed:{
            //原始数值
            num(){
                return this.weiboDatas.num
            }
        },
    }

    
</script>

<style scoped>
    #weiboData{
        background-image: url('../assets/wb-li.png');
        background-size: 100% 100%;
    }

    #weiboData #dataName{
        width: 100%;
        height: 35%;
        color: white;
        font-family: 'Microsoft YaHei';
        display: flex;
        flex-direction: row; /*主轴*/
        align-items: center; /*侧轴对齐方式*/
        justify-content: center; /*主轴对齐方式*/
    }

    #weiboData #dataNum{
        width: 100%;
        height: 65%;
        font-weight: bold;
        font-family: 'D-din';
        display: flex;
        flex-direction: row; /*主轴*/
        align-items: center; /*侧轴对齐方式*/
        justify-content: center; /*主轴对齐方式*/
    }
</style>