<template>
    <div id="monthWarning">
        <div id="bk1"></div>
        <div id="warningTitle">
            <span id="warningTitleText">本月预警情况</span>
        </div>
        <div id="bk2"></div>
        <div id="warningLine"></div>
    </div>
</template>

<script>
    import * as echarts from 'echarts'
    
    export default {
        name: 'WarningLine',
        data(){
            return {
                option: {
                    //图例
                    legend: {
                        data: ['正面','负面'],
                        icon: 'rect',
                        itemWidth: 16,
                        itemHeight: 16,
                        textStyle: {
                            color: 'white',
                            fontFamily: 'Microsoft YaHei',
                            fontSize: 16,
                        },
                        left: 140,
                        top: 3, 
                    },
                    //位置
                    grid: {
                        left: '1%',
                        right: '1%',
                        top: '60px',
                        bottom: '13%',
                    },
                    tooltip: {
                        renderMode: 'html',
                        className: 'pieTooltip',
                        trigger: 'axis',
                        backgroundColor: 'rgba(26, 26, 26, 0.3)',
                        padding: [12, 10],  //高度使用padding和height结合调整
                        textStyle: {
                            color: 'white',
                            lineHeight: 15.84,
                        },
                        extraCssText: 'width: 135px; height: 43px;',
                        formatter(params){
                            var src = ''

                            for(var x in params)
                            {
                                src += params[x].marker + params[x].seriesName + '&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp' + params[x].value[params[x].componentIndex+1] + '<br>'
                            }

                            return src
                        },
                    },
                    color: ['#4390f6', '#f9e64e'],
                    //数据
                    // dataset: {
                    //     source: [
                    //         ['01', 20, 15],
                    //         ['02', 30, 25],
                    //         ['03', 40, 15],
                    //         ['04', 45, 30],
                    //         ['05', 30, 40],
                    //         ['06', 30, 45],
                    //         ['07', 25, 90],
                    //         ['08', 20, 95],
                    //         ['09', 25, 75],
                    //         ['10', 65, 60],
                    //         ['11', 20, 15],
                    //         ['12', 30, 25],
                    //         ['13', 40, 15],
                    //         ['14', 45, 30],
                    //         ['15', 30, 40],
                    //         ['16', 30, 45],
                    //         ['17', 25, 90],
                    //         ['18', 20, 95],
                    //         ['19', 25, 75],
                    //         ['20', 65, 60],
                    //         ['21', 20, 15],
                    //         ['22', 30, 25],
                    //         ['23', 40, 15],
                    //         ['24', 45, 30],
                    //         ['25', 30, 40],
                    //         ['26', 30, 45],
                    //         ['27', 25, 90],
                    //         ['28', , ],
                    //         ['29', , ],
                    //         ['30', , ],
                    //     ],
                    // },
                    dataset: {
                        source: [],
                    },
                    //x轴
                    xAxis: {
                        type: 'category',
                        axisLine: {
                            show: false,
                        },
                        axisTick: {
                            show: false,
                        },
                        axisLabel: {
                            color: 'white',
                            margin: 8,
                            padding: [2,5,1,5],
                            fontFamily: 'D-din',
                            fontSize: 14,
                        },
                        splitLine: {
                            show: false,
                        },
                        splitArea: {
                            show: true,
                            interval: 0,
                            areaStyle: {
                                opacity: 0.5,
                            },
                        },
                        
                    },
                    //y轴
                    yAxis: {
                        show: false,
                        splitLine: {
                            show: false,
                        },
                    },
                    //系列图标配置
                    series: [
                        {
                        name: '正面',
                        type: 'line',
                        symbol: 'circle',
                        symbolSize: 10,
                        //k从0走到1
                        //return在当前时刻走到那个位置，比如k，k*k
                        // animationEasing: function(k){
                        //         if(k<0.2)
                        //         {
                        //             return 0
                        //         }
                        //         else if(k<0.4)
                        //         {
                        //             return 2 * k - 0.4
                        //         }
                        //         else
                        //         {
                        //             return k
                        //         }
                        //     },
                        },
                        {
                        name: '负面',
                        type: 'line',
                        symbol: 'circle',
                        symbolSize: 10,
                        // animationEasing: function(k){
                        //         if(k<0.2)
                        //         {
                        //             return 0
                        //         }
                        //         else if(k<0.4)
                        //         {
                        //             return 2 * k - 0.4
                        //         }
                        //         else
                        //         {
                        //             return k
                        //         }
                        //     },
                        }
                    ],
                    //滚动条
                    dataZoom: {
                        id: 'dataZoom',
                        type: 'slider',
                        show: true,
                        showDataShadow: false,
                        startValue: 0,
                        endValue: 19,
                        height: '10',
                        bottom: '5',
                        zoomLock: true,
                        brushSelect: false,
                        backgroundColor: 'rgba(119, 227, 239, 0.1)',
                        fillerColor: 'rgba(119, 227, 239, 0.5)',
                    },
                    animation: true,
                    //控制线的绘制速度
                    animationDuration: 6000,
                    animationDurationUpdate: 6000,
                    //控制点的绘制速度
                    animationDelay: function (idx) {
                        return idx * 300
                    },
                },
                warningLine: null,
                lineData: [],
            }
        },
        mounted(){
            this.warningLine = echarts.init(document.getElementById('warningLine'))
            this.setData(window.parent.getMonthlyData())  //在setData里setOption

            setInterval(()=>{
                this.setData(window.parent.getMonthlyData())
            }, 300000)

            window.onload = ()=>{
                let el = document.getElementById('warningLine')
                let width = getComputedStyle(el).width
                let height = getComputedStyle(el).height
                let h = parseFloat(height)
                let w = parseFloat(width)
                //以上这些是计算宽度的px并转化为number类型
                //根据宽度分段列举
                let gridBottom = '13%'
                if(w<500)
                {
                    gridBottom = '25%'
                }
                else if(w>= 500 && w<750)
                {
                    gridBottom = '25%'
                }
                else if(w>= 750 && w<1000)
                {
                    gridBottom = '20%'
                }
                else if(w>= 1000 && w<1500)
                {
                    gridBottom = '13%'
                }
                else if(w>=1500)
                {
                    gridBottom = '13%'
                }

                let legendTop = h * 3 / 70 - 720 / 70
                let chartTopNum = 0.03 * w + 25
                let chartTop = chartTopNum.toString() + 'px'

                let nop = {
                    legend:{
                        top: legendTop,
                    },
                    grid:{
                        top: chartTop,
                        bottom: gridBottom,
                    }
                }
                this.warningLine.setOption(nop)
            }
            window.onresize = ()=>{
                let el = document.getElementById('warningLine')
                let width = getComputedStyle(el).width
                let height = getComputedStyle(el).height
                let h = parseFloat(height)
                let w = parseFloat(width)
                //以上这些是计算宽度的px并转化为number类型
                //根据宽度分段列举
                let gridBottom = '13%'
                if(w<500)
                {
                    gridBottom = '25%'
                }
                else if(w>= 500 && w<750)
                {
                    gridBottom = '25%'
                }
                else if(w>= 750 && w<1000)
                {
                    gridBottom = '20%'
                }
                else if(w>= 1000 && w<1500)
                {
                    gridBottom = '13%'
                }
                else if(w>=1500)
                {
                    gridBottom = '13%'
                }

                let legendTop = h * 3 / 70 - 720 / 70
                let chartTopNum = 0.03 * w + 25
                let chartTop = chartTopNum.toString() + 'px'

                let nop = {
                    legend:{
                        top: legendTop,
                    },
                    grid:{
                        top: chartTop,
                        bottom: gridBottom,
                    }
                }
                this.warningLine.setOption(nop)

                this.warningLine.resize()
            }
        },
        methods:{
            setData(listData){
                this.lineData = listData.data
                //改x轴的坐标
                for(let i=0; i<this.lineData.length; i++)
                {
                    this.lineData[i][0] = this.lineData[i][0].slice(5, 13)
                }

                this.option.dataset = {
                    source: this.lineData,
                }
                this.warningLine.setOption(this.option)
            }

        },
    }

    
</script>

<style scoped>
    #monthWarning {
        width: 100vw;  /*2002*/
        height: 100vh;
    }

    #warningTitle {
        width: 100%; /*1988*/
        height: 10vh;
        line-height: 10vh;
        color: white;
        font-family: 'Microsoft YaHei';
        font-size: 18px;
        font-weight: bold;
        text-indent: 1em;
        position: absolute;
        top: 0px;
        z-index: 2;
    }

    #bk1{
        width: 100%; /*1988*/
        height: 10vh;
        background: url(../assets/上半部分.png) 0 0 no-repeat;
        background-size: 100% 100%;
    }

    #bk2{
        width: 100%;
        height: 90vh;
        background: url(../assets/下半部分.png) 0 0 no-repeat;
        background-size: 100% 100%;
    }

    #warningLine {
        position: absolute;
        top: 0px;
        z-index: 3;
        width: 100%;
        height: 100%;
    }
</style>