        methods:{
            setShowOption(e){
                let op = this.option
                e.setOption(op)
            },
            setNodesOption(e){
                let n = this.nodes.length
                let nameArray = []
                for(let i=0; i<n; i++)
                {
                    if(nameArray.indexOf(this.nodes[i].name) == -1)
                    {
                        nameArray.push(this.nodes[i].name)
                        this.nodes[i].id = this.nodes[i].name
                    }
                    if(this.nodes[i].nodeData.spreadLayer)
                    {
                        if(this.nodes[i].nodeData.spreadLayer == '第1层')
                        {
                            this.nodes[i].symbol = 'image://' + require('../assets/circle_first.png')
                            this.nodes[i].symbolSize = 250
                        }
                        else if(this.nodes[i].nodeData.spreadLayer == '第2层')
                        {
                            this.nodes[i].symbol = 'image://' + require('../assets/circle_second.png')
                            this.nodes[i].symbolSize = 200
                        }
