                            this.nodes[i].symbolSize = 100
                        }
                    }
                    else
                    {
                        this.nodes[i].symbol = 'image://' + require('../assets/circle_fourth.png')
                        this.nodes[i].symbolSize = 100
                    }
                }

                let op = {
                    series: [
                        {
                            data: this.nodes,
                        }
                    ]
                }
                e.setOption(op)
            },
