//引入vue
import Vue from 'vue'
//引入App组件，所有组件的父组件
import App from './App.vue'
//引入echarts
import echarts from 'echarts'
Vue.prototype.$echarts = echarts
//引入
import jquery from 'jquery'
Vue.prototype.$ = jquery

import VueScroll from '@david-j/vue-j-scroll';
Vue.use(VueScroll);

//关闭生产提示
Vue.config.productionTip = false

//创建实例对象
new Vue({
  el: '#app',
  render: h => h(App),
})