<template>
    <div id="weiboSummary">
        <div id="weiboSummaryTitle">
            <span>微博概况</span>
        </div>
        <div id="weiboSummaryContent">
            <div id="weiboSummaryContentData" :style="{width: wcdWidth}">
                <WeiboData  ref="childs" v-for="wb in weiboDatas" :key="wb.id" :weiboDatas="wb"></WeiboData>
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import WeiboData from './WeiboData.vue'
    import $ from 'jquery'

    export default {
        name: 'WeiboSummary',
        components: {WeiboData},
        data(){
            return {
                weiboDatas: [
                    {id: '001', title: '博文总数', num: 0, color: '#ffffff', haveBFH: false},
                    {id: '002', title: '正面', num: 0, color: '#48ece7', haveBFH: false},
                    {id: '003', title: '负面', num: 0, color: '#fc704a', haveBFH: false},
                    {id: '004', title: '原创微博', num: 0, color: '#fbc142', haveBFH: false},
                    {id: '005', title: '受众人次', num: 0, color: '#2797e6', haveBFH: true},
                ],

                //样式数据
                wcdWidth: '98%',  //控制左右间隔

            }
        },
        mounted(){
            this.setData(window.parent.getWeiBoGaiKuangData())
            setInterval(() => {
                this.setData(window.parent.getWeiBoGaiKuangData())
            }, 30000);

            window.onload = ()=>{
                //获得总宽高
                let el = document.getElementById('weiboSummary')
                let _width = getComputedStyle(el).width
                let _height = getComputedStyle(el).height
                let w = parseFloat(_width)
                let h = parseFloat(_height)
                //标题
                let title = document.getElementById('weiboSummaryTitle')

                this.titleLineHeight =  parseFloat(getComputedStyle(title).height)
                if(w<500)
                {
                    this.wcdWidth = '95%'
                }
                else if(w>=500 && w<800)
                {
                    this.wcdWidth = '96%'
                }
                else if(w>=1100)
                {
                    this.wcdWidth = '98%'
                }
                
                //5个子组件修改样式
                this.$refs.childs[0].resize(w, h)
                this.$refs.childs[1].resize(w, h)
                this.$refs.childs[2].resize(w, h)
                this.$refs.childs[3].resize(w, h)
                this.$refs.childs[4].resize(w, h)
            }
            window.onresize = ()=>{
                //获得总宽高
                let el = document.getElementById('weiboSummary')
                let _width = getComputedStyle(el).width
                let _height = getComputedStyle(el).height
                let w = parseFloat(_width)
                let h = parseFloat(_height)
                //标题
                let title = document.getElementById('weiboSummaryTitle')

                this.titleLineHeight =  parseFloat(getComputedStyle(title).height)
                if(w<500)
                {
                    this.wcdWidth = '95%'
                }
                else if(w>=500 && w<800)
                {
                    this.wcdWidth = '96%'
                }
                else if(w>=1100)
                {
                    this.wcdWidth = '98%'
                }
                
                //5个子组件修改样式
                this.$refs.childs[0].resize(w, h)
                this.$refs.childs[1].resize(w, h)
                this.$refs.childs[2].resize(w, h)
                this.$refs.childs[3].resize(w, h)
                this.$refs.childs[4].resize(w, h)
            }
        },
        methods:{
            //获取数据的函数
            //应当在这里完成：向后端发送传值请求、获取数据、解析数据、封装成需要的形式并返回等
            setData(listData){
                var a = listData
                Vue.set(this.weiboDatas[0], 'num', a.微博总量)
                Vue.set(this.weiboDatas[1], 'num', a.正面)
                Vue.set(this.weiboDatas[2], 'num', a.负面)
                Vue.set(this.weiboDatas[3], 'num', a.微博原创)
                Vue.set(this.weiboDatas[4], 'num', a.约传播受众人次)
            },
            
            //查看数据
            cancanNeedData()
            {
                var i
                // 请求域名
                var domain = "https://dt-api-test.xunku.org";
                $.ajax({
                    type: "post",
                    url:
                        domain +
                        "/login/login?action=portal.AppLogin&userName=csdlzy&password=csdlzy970&vcode=&vcodeCorrect=&platformCode=ca6887c8ed71c91a3a950c3ca50f96cf&columnName=Name",
                    async: false, //是否异步
                    crossDomain: true,
                    xhrFields: { withCredentials: true },
                    dataType: "json",
                    success: function (result1) {
                        var statusCode = result1.statusCode; //返回是否成功登陆的状态
                        this.data_token = result1.token; //登陆成功时返回token
                        window.sessionStorage.setItem("data_token", this.data_token);

                        var jsonGetData = {
                            token: window.sessionStorage.getItem("data_token"),
                        };

                        $.ajax({
                            type: "post",
                            url: domain + '/eventMonitor/getWeiboProfile',
                            data: JSON.stringify(jsonGetData),
                            contentType: "application/json;charset=utf-8",
                            crossDomain: true,
                            async: false,
                            headers: {
                                "x-auth-token":
                                    window.sessionStorage.getItem("data_token"),
                            },
                            xhrFields: { withCredentials: true },
                            dataType: "json",
                            success: function (resultData) {
                                console.log(resultData)
                            },
                        });
                    },
                });
            },
        },
    }

    
</script>

<style scoped>
    #weiboSummary{
        /*1381 284*/
        width: 100vw;
        height: 100vh;
    }
    #weiboSummaryTitle{
        width: 100%;
        height: 10vh;
        line-height: 10vh;
        font-size: 18px;
        font-weight: bold;
        text-indent: 1em;
        background: url(../assets/上半部分.png) 0 0 no-repeat;
        background-size: 100% 100%;
        color: white;
    }

    #weiboSummaryContent{
        width: 100%;
        height: 100%;
        background: url(../assets/下半部分.png) 0 0 no-repeat;
        background-size: 100% 100%;
        display: flex;
        flex-direction: column; /*主轴*/
        align-items: center; /*侧轴对齐方式*/
        justify-content: center; /*主轴对齐方式*/
    }

    #weiboSummaryContentData{
        height: 80%;
        display: flex;
        flex-direction: row; /*主轴*/
        align-items: center; /*侧轴对齐方式*/
        justify-content: space-between; /*主轴对齐方式*/
    }
</style>