<template>
    <div id="hotDoorComment">
        <div id="hotDoorCommentContent">
            <vue-j-scroll :data="commentDatas" :steep="0.5" scrollDirection="top" style="width: 100%;height: 100%;overflow: hidden;">
                <CommentItem v-for="cd in commentDatas" :key="cd.id" :commentData="cd"></CommentItem>
            </vue-j-scroll>
        </div>
   </div>
</template>

<script>
    import CommentItem from './CommentItem.vue';
    import $ from 'jquery'
    import moment from "moment"

    export default {
        naem: 'HotDoorComment',
        components: {CommentItem},
        data() {
            return {
                commentDatas:[
                    {
                        id: '001',
                        img: '',
                        name: '',
                        content: '',
                        time: '',
                        from: '',
                    },
                    {
                        id: '002',
                        img: '',
                        name: '',
                        content: '',
                        time: '',
                        from: '',
                    },
                    {
                        id: '003',
                        img: '',
                        name: '',
                        content: '',
                        time: '',
                        from: '',
                    },
                    {
                        id: '004',
                        img: '',
                        name: '',
                        content: '',
                        time: '',
                        from: '',
                    },
                ],
                tempData: [],
            };
        },
        mounted(){
            this.getData()
            this.setData()

            setInterval(()=>{
                this.getData()
                this.setData()
            }, 300000)
        },

        methods:{
            setData(){
                length = this.tempData.length
                this.commentDatas = []
                for(let i=0; i<length; i++)
                {
                    let temp = {
                        'id': i,
                        'img': this.tempData[i].img,
                        'name': this.tempData[i].author,
                        'content': this.tempData[i].content,
                        'time': this.tempData[i].date,
                        'from': this.tempData[i].source,
                        'bkcolor': '',
                        'textcolor': '',
                    }
                    //颜色
                    if(i%2 == 0)
                    {
                        temp.bkcolor =  'rgb(255, 255, 124)'
                        temp.textcolor = '#c76021'
                    }
                    else
                    {
                        temp.bkcolor =  'rgb(219, 142, 35)'
                        temp.textcolor = '#ffffff'
                    }
                    //内容
                    if(temp.content.length > 40)
                    {
                        temp.content = temp.content.slice(0, 30) + '...'
                    }
                    //头像
                    if(temp.img == '')
                    {
                        temp.img = require('../assets/头像1.png')
                    }

                    this.commentDatas.push(temp)
                }
            },

            //查看数据
            getData()
            {
                //时间
                let et = moment().format('YYYY-MM-DD HH:ss:mm')
                let st = moment().subtract(7, 'days').format('YYYY-MM-DD HH:ss:mm')

                // 请求域名
                var domain = "https://dt-api.xunku.org";
                $.ajax({
                    type: "post",
                    url:
                        domain +
                        "/login/login?action=portal.AppLogin&userName=cmtea&password=cmtea398&vcode=&vcodeCorrect=&platformCode=41e1fbcd4355ac6731376a8340a2cb0f&columnName=Name",
                    async: false, //是否异步
                    crossDomain: true,
                    xhrFields: { withCredentials: true },
                    dataType: "json",
                    success: (result1)=>{
                        var statusCode = result1.statusCode; //返回是否成功登陆的状态
                        this.data_token = result1.token; //登陆成功时返回token
                        window.sessionStorage.setItem("data_token", this.data_token);

                        var jsonGetData = {
                            token: window.sessionStorage.getItem("data_token"),
                            startTime: st,
                            endTime: et,
                        };

                        $.ajax({
                            type: "post",
                            url: domain + '/twentyBig/conferenceMeeting/hotComments',
                            data: JSON.stringify(jsonGetData),
                            contentType: "application/json;charset=utf-8",
                            crossDomain: true,
                            async: false,
                            headers: {
                                "x-auth-token":
                                    window.sessionStorage.getItem("data_token"),
                            },
                            xhrFields: { withCredentials: true },
                            dataType: "json",
                            success: (resultData) =>{
                                //console.log(resultData)
                                this.tempData = resultData.data
                                // console.log(this)
                            },
                        });
                    },
                });
            },
        },
};
</script>

<style scoped>
    *{
        margin: 0px;
        padding: 0px;
    }

    #hotDoorComment{
        width: 100vw;
        height: 100vh;
        display: flex;
        flex-direction: column; /*主轴*/
        align-items: center; /*侧轴对齐方式*/
        justify-content: center; /*主轴对齐方式*/
    }

    #hotDoorCommentContent{
        width: 100%;
        height: 100%;
    }
</style>