<template>
    <div id="emotionPie">
        <div id="emotionPieTitle">
            <span>媒体平台性质统计</span>
        </div>
        <div id="pieChart"></div>
    </div>
</template>

<script>
    import * as echarts from 'echarts'
    import * as math from 'mathjs'
    import $ from 'jquery'

    var colorPos = new echarts.graphic.LinearGradient(0,0,0,1,[{offset: 0, color: "#6ff4fd" }, { offset: 1, color: "#3b8882" }])
    var colorNeu = new echarts.graphic.LinearGradient(0,0,0,1,[{offset: 0, color: "#0e3795" }, { offset: 1, color: "#477df4" }])
    var colorNeg = new echarts.graphic.LinearGradient(0,1,0,0,[{offset: 0, color: "#fcee6b" }, { offset: 1, color: "#837724" }])

    export default {
        name: 'EchartsPie',
        data(){
            return {
                option: {
                    //标题(已改为使用html)
                    //图例
                    legend: {
                        orient: 'vertical',
                        left: '2%',
                        top: '5%',
                        icon: 'circle',
                        itemWidth: 16,
                        itemHeight: 16,
                        itemGap: 15,
                        textStyle: {
                            color: 'white',
                            fontFamily: 'Microsoft YaHei',
                            padding: [0,0,0,10],
                            fontSize: 14,
                        },
                    },
                    tooltip: {
                        renderMode: 'html',
                        className: 'pieTooltip',
                        backgroundColor: 'rgba(26, 26, 26, 0.3)',
                        padding: [15, 10],  //高度使用padding和height结合调整
                        textStyle: {
                            color: 'white',
                        },
                        extraCssText: 'width: 135px; height: 18px;',
                        confine: true,
                        formatter(params){
                            let src = ''

                            //显示原始数据
                            src += params.marker + '&nbsp&nbsp' + params.name + '&nbsp&nbsp' + params.data.oldValue

                            return src
                        }
                    },
                    //饼图配置
                    series: {
                        type: 'pie',
                        color: [colorNeu, colorNeg, colorPos],
                        // color: [colorPos, colorNeu, colorNeg],
                        roseType: 'area',
                        radius: ['15%', '90%'],
                        top: '5%',
                        bottom: '5%',
                        itemStyle: {
                            borderRadius: 6,
                        },
                        label: {
                            show: true,
                            fontFamily: 'Microsoft YaHei',
                            color: 'white',
                        },
                        labelLine:{
                            length: 15,
                            length2: 15,
                        },
                        //顺序：中、负、正
                        data: [
                            {name: '中性', oldValue: 80, value: 80},
                            {name: '负面', oldValue: 60, value: 60},
                            {name: '正面', oldValue:100, value: 100},
                        ]
                    }
                },
                emotionPie: null,
                pieData: [],
            }
        },
        mounted(){
            this.emotionPie = echarts.init(document.querySelector('#pieChart'))
            this.emotionPie.setOption(this.option)
            
            this.setData(window.parent.getMeiTiXingZhiData())
            //定时更新
            setInterval(()=>{
                this.setData(window.parent.getMeiTiXingZhiData())
            }, 300000)

            window.onload = ()=>{
                let el = document.getElementById('emotionPie')
                let width = getComputedStyle(el).width
                let w = parseFloat(width)

                //需要改的
                let le_itemWH = 16  //legend宽高
                let le_itemGap = 15  //legend间隔
                let le_fontSize = 14  //legend字号
                let pie_radius = ['15%', '90%']  //饼图最小和最大环
                let pie_bottom = '15%'  //据底部距离
                let pie_fontSize = 12  //标注字号
                let pie_lineLen1 = 15  //标注引导线一段长度
                let pie_lineLen2 = 15  //二段长度

                if(w<300)
                {
                    le_itemWH = 9
                    le_itemGap = 5
                    le_fontSize = 8
                    pie_radius = ['5%', '50%']
                    pie_bottom = '25%'
                    pie_fontSize = 8
                    pie_lineLen1 = 5
                    pie_lineLen2 = 5
                }
                else if(w>=300 && w<=400)
                {
                    le_itemWH = 12
                    le_itemGap = 8
                    le_fontSize = 10
                    pie_radius = ['10%', '60%']
                    pie_bottom = '20%'
                    pie_fontSize = 10
                    pie_lineLen1 = 8
                    pie_lineLen2 = 8
                }
                else if(w>=400 && w<=650)
                {
                    pie_radius = ['10%', '80%']
                    pie_lineLen1 = 14
                    pie_lineLen2 = 14
                }

                let op = {
                    legend:{
                        itemWidth: le_itemWH,
                        itemHeight: le_itemWH,
                        itemGap: le_itemGap,
                        textStyle: {
                            fontSize: le_fontSize,
                        },
                    },
                    series:{
                        radius: pie_radius,
                        //bottom: pie_bottom,
                        label:{
                            fontSize: pie_fontSize,
                        },
                        labelLine:{
                            length: pie_lineLen1,
                            length2: pie_lineLen2,
                        },
                    },
                }

                this.emotionPie.setOption(op)
            }
            //以下全部为自适应部分,可以直接折叠
            window.onresize = ()=>{
                let el = document.getElementById('emotionPie')
                let width = getComputedStyle(el).width
                let w = parseFloat(width)

                //需要改的
                let le_itemWH = 16  //legend宽高
                let le_itemGap = 15  //legend间隔
                let le_fontSize = 14  //legend字号
                let pie_radius = ['15%', '90%']  //饼图最小和最大环
                let pie_bottom = '15%'  //据底部距离
                let pie_fontSize = 12  //标注字号
                let pie_lineLen1 = 15  //标注引导线一段长度
                let pie_lineLen2 = 15  //二段长度

                if(w<300)
                {
                    le_itemWH = 9
                    le_itemGap = 5
                    le_fontSize = 8
                    pie_radius = ['5%', '50%']
                    pie_bottom = '25%'
                    pie_fontSize = 8
                    pie_lineLen1 = 5
                    pie_lineLen2 = 5
                }
                else if(w>=300 && w<=400)
                {
                    le_itemWH = 12
                    le_itemGap = 8
                    le_fontSize = 10
                    pie_radius = ['10%', '60%']
                    pie_bottom = '20%'
                    pie_fontSize = 10
                    pie_lineLen1 = 8
                    pie_lineLen2 = 8
                }
                else if(w>=400 && w<=650)
                {
                    pie_radius = ['10%', '80%']
                    pie_lineLen1 = 14
                    pie_lineLen2 = 14
                }

                let op = {
                    legend:{
                        itemWidth: le_itemWH,
                        itemHeight: le_itemWH,
                        itemGap: le_itemGap,
                        textStyle: {
                            fontSize: le_fontSize,
                        },
                    },
                    series:{
                        radius: pie_radius,
                        //bottom: pie_bottom,
                        label:{
                            fontSize: pie_fontSize,
                        },
                        labelLine:{
                            length: pie_lineLen1,
                            length2: pie_lineLen2,
                        },
                    },
                }

                this.emotionPie.setOption(op)
                this.emotionPie.resize()
            }
        },
        methods:{
            //查看数据
            cancanNeedData()
            {
                // 请求域名
                var domain = "https://dt-api-test.xunku.org";
                $.ajax({
                    type: "post",
                    url:
                        domain +
                        "/login/login?action=portal.AppLogin&userName=csdlzy&password=csdlzy970&vcode=&vcodeCorrect=&platformCode=ca6887c8ed71c91a3a950c3ca50f96cf&columnName=Name",
                    async: false, //是否异步
                    crossDomain: true,
                    xhrFields: { withCredentials: true },
                    dataType: "json",
                    success: function (result1) {
                        var statusCode = result1.statusCode; //返回是否成功登陆的状态
                        this.data_token = result1.token; //登陆成功时返回token
                        window.sessionStorage.setItem("data_token", this.data_token);

                        var jsonGetData = {
                            token: window.sessionStorage.getItem("data_token"),
                        };

                        $.ajax({
                            type: "post",
                            url: domain + '/mediaUniversity/subjectMonitor/platformNature',
                            data: JSON.stringify(jsonGetData),
                            contentType: "application/json;charset=utf-8",
                            crossDomain: true,
                            async: false,
                            headers: {
                                "x-auth-token":
                                    window.sessionStorage.getItem("data_token"),
                            },
                            xhrFields: { withCredentials: true },
                            dataType: "json",
                            success: function (resultData) {
                                console.log(resultData.data)
                            },
                        });
                    },
                });
            },

            setData(listData){
                this.pieData = listData.data
                let showPieData = []
                for(let i=0; i<3; i++)
                {
                    showPieData[i] = math.round(math.log(this.pieData[i].value, math.e), 4)
                }

                let newOption = {
                    series: {
                        //oldValue原始数据 --> 取ln --> value显示数据
                        data: [
                            {'name': this.pieData[1].name,'oldValue': this.pieData[1].value, 'value': showPieData[1]},
                            {'name': this.pieData[2].name,'oldValue': this.pieData[2].value, 'value': showPieData[2]},
                            {'name': this.pieData[0].name,'oldValue': this.pieData[0].value, 'value': showPieData[0]},
                        ]
                    }
                }

                this.emotionPie.setOption(newOption)
            }
        }
    }

    
</script>

<style scoped>
    #emotionPie {
        width: 100vw;
        height: 100vh;
        /* background-color: rgba(26, 26, 26, 0.3);*/
    }

    #emotionPieTitle {
        height: 10vh;
        width: 100%;
        font-family: 'Microsoft YaHei';
        font-size: 18px;
        font-weight: bold;
        text-indent: 1em;
        color: white;
        line-height: 10vh;  /*文字垂直居中效果*/
        background: url(../assets/上半部分.png) 0 0 no-repeat;
        background-size: 100% 100%;
        /* clip-path: polygon(100% 0%, 100% 0%, 100% 0%, 100% 40%, 96% 100%, 0% 100%, 0% 100%, 0% 0%);
        background-color: rgba(26, 26, 26, 0.3); */
    }

    #pieChart {
        width: 100%;
        height: 90vh;
        background: url(../assets/下半部分.png) 0 0 no-repeat;
        background-size: 100% 100%;
    }
</style>