<template>
    <div id="polarBar">
        <!-- <div id="polarBarTitle">
            <span>活跃国家</span>
        </div> -->
        <div id="polarBarChart"></div>
    </div>
</template>

<script>
    import * as echarts from 'echarts'
    var color1 = new echarts.graphic.LinearGradient(0,0,0,1,[{offset: 0, color: 'rgba(255, 255, 255, 1)' }, { offset: 1, color: 'rgba(255, 255, 255, 0.2)' }])
    
    export default {
        name: 'RollRollRoll',
        data(){
            return {
                option: {
                    polar: {
                        radius: ['15%', '80%']
                    },
                    angleAxis: {
                        startAngle: 90,
                        type: 'value',
                        splitNumber: 12,
                        max: 600,
                        axisLine: {
                            show: true,
                            lineStyle:{
                                color: 'white',
                            },
                        },
                        axisTick: {
                            show: false,
                        },
                        axisLabel: {
                            show: true,
                            color: 'white'
                        },
                        splitLine: {
                            show: true,
                            lineStyle:{
                                color: ['rgba(255, 255, 255, 0.8)', 'rgba(255, 255, 255, 0.4)'],
                            },  
                        }
                    },
                    radiusAxis: {
                        //show: false,
                        type: 'category',
                        //scale: false,
                        max: 5,
                        axisLabel: {
                            show: true,
                            interval: 0,
                            margin: 15,
                            color: 'white',
                            fontSize: 14,
                        },
                        axisTick: {
                            show: false,
                        },
                        axisLine: {
                            show: false,
                        }
                    },
                    color: ['#7DBFBF' , '#73B5B5', '#64ABAB', '#4BA7A7', '#439797', '#357C7C'],
                    series: {
                        type: 'bar',
                        colorBy: 'data',
                        coordinateSystem: 'polar',
                        roundCap: true,
                        barWidth: '35%',
                        //barGap: '100%',
                        //barMinAngle: 60,
                        data: [
                            ['', 0],
                            ['新加坡', 200],
                            ['印度', 250],
                            ['迪拜', 380],
                            ['伊朗', 460],
                            ['阿富汗', 525],
                        ]
                    }
                },
                polarBar: null,
            }
        },
        mounted(){
            this.polarBar = echarts.init(document.querySelector('#polarBarChart'))
            this.polarBar.setOption(this.option)
            
            //定时更新
            setInterval(()=>{
                console.log('更新！')
                this.updateOption()
            }, 10000)

            
            //以下全部为自适应部分,可以直接折叠
            window.onresize = ()=>{
                this.polarBar.resize()
            }
        },
        methods:{
            //模拟获取数据
            //应当在这里完成：向后端发送传值请求、获取数据、解析数据、封装成需要的形式并返回等
            //只负责数据，其他与option相关的事情不在这里完成
            getData(){
                return [
                    {
                        name: '新加坡',
                        value: 200,
                    },
                    {
                        name: '印度',
                        value: 250,
                    },
                    {
                        name: '迪拜',
                        value: 300,
                    },
                ]
            },
            //模拟更新数据
            //应当在这里完成：调用获取数据函数，根据需要组装新的option并返回
            updateOption(){
                let d = this.getData()
                let newData = []
                for(let i=0; i<3; i++)
                {
                    newData.push([d[i].name, d[i].value])
                }

                let op = {
                    series: {
                        data: newData,
                    }
                }

                this.polarBar.setOption(op)
            },
        }
    }

    
</script>

<style scoped>
    #polarBar {
        width: 100vw;
        height: 100vh;
        background-color: rgba(18, 76, 88, 0.5);
    }

